import Vue from "vue";
import VueRouter from "vue-router";
import viewLanding from "../layouts/viewLanding.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "viewLanding",
    component: viewLanding
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
